import React, { useEffect, useState } from "react";
import { NavLink } from "react-router-dom";

const Navbar = () => {
  const [slideHeight, setSlideHeight] = useState(0);
  const [slideTop, setSlideTop] = useState(0);
  const [slideWidth, setSlideWidth] = useState(0);

  const Slide = (e) => {
    setSlideHeight(e.target.offsetHeight);
    setSlideTop(e.target.offsetTop);
    setSlideWidth(e.target.offsetWidth);
  };

  useEffect(() => {
    const items = document.querySelectorAll("nav ul a");

    const filter = [...items].filter((all) => {
      const filtered = "/" + all.innerHTML.toLowerCase();
      return filtered === location.pathname;
    });

    if (filter.length !== 0) {
      setSlideTop(filter[0].offsetTop);
      setSlideHeight(filter[0].offsetHeight);
      setSlideWidth(filter[0].offsetWidth);
    } else {
      setSlideTop(items[0].offsetTop);
      setSlideHeight(items[0].offsetHeight);
      setSlideWidth(items[0].offsetWidth);
    }
  }, []);

  return (
    <nav>
      <NavLink
        to="/"
        className="brand"
        onClick={() => {
          const items = document.querySelectorAll("nav ul a");

          setSlideTop(items[0].offsetTop);
          setSlideHeight(items[0].offsetHeight);
          setSlideWidth(items[0].offsetWidth);
        }}
      >
        Shams Visuals
      </NavLink>

      <div
        className="hamburger"
        onClick={() => {
          document.querySelector(".hamburger").classList.toggle("active");
          document.querySelector("nav ul").classList.toggle("active");
        }}
      >
        <span></span>
        <span></span>
        <span></span>
      </div>
      <ul>
        <div className="parentIndi">
          <div
            className="indicator"
            style={{ width: slideWidth, top: slideTop + slideHeight - 2 }}
          ></div>
          <NavLink exact to="/" onClick={Slide}>
            Home
          </NavLink>
          <NavLink to="/about" onClick={Slide}>
            About
          </NavLink>
          <NavLink to="/contact" onClick={Slide}>
            Contact
          </NavLink>
          <NavLink to="/service" onClick={Slide}>
            Service
          </NavLink>
        </div>
      </ul>
    </nav>
  );
};

export default Navbar;
